<template>
  <div class="hello">
    <video id="example_video_1" class="video-js vjs-default-skin" autoplay="autoplay" controls preload="none"  data-setup="{}" style="object-fit: fill;width: 100%;height: 100%;">
      <source src="./gift.mp4" type="video/mp4">
      <!-- <source src="./gift.mp4" type="video/webm">
      <source src="./gift.mp4" type="video/ogg"> -->
      <track kind="captions" src="../shared/example-captions.vtt" srclang="en" label="English"></track>
      <!-- Tracks need an ending tag thanks to IE9 -->
      <track kind="subtitles" src="../shared/example-captions.vtt" srclang="en" label="English"></track>
      <!-- Tracks need an ending tag thanks to IE9 -->
      <p class="vjs-no-js">To view this video please enable JavaScript, and consider upgrading to a web browser that <a href="http://videojs.com/html5-video-support/" target="_blank">supports HTML5 video</a></p>
    </video>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
</style>



